import Image from 'next/image';

export default function ServiceCard({icon, iconAlt, title, description}: {icon: any, iconAlt: string, title: string, description: string}) {
    return (
      <div className="flex flex-row items-center justify-center bg-projectWhite">
        <Image src={icon} alt={iconAlt}/>
        <div className="flex flex-col">
            <h3>{title}</h3>
            <h4>{description}</h4>
        </div>
      </div>
    );
}